

$bg:rgb(16,16,16);

.banner{
	height: 700px;
	background-color: $bg;
	background-size: cover;
	background-repeat: no-repeat;
	position: relative;

	&::after{
		content: "";
		width: 100%;
		height: 100%;
		position: absolute;
		opacity: 0.4;
	}

	h1, p{
		color: white;
		font-size: 3rem;
		widows: 50%;
		z-index: 10;
	}
	p{
		font-size: 2rem;
		letter-spacing: 2px;
		word-spacing: 2px;
		font-weight: 100;
	}
	
}
.row{
	background-color: $bg;
	padding: 1rem;
	>h2{
		margin: 0.3rem;
		font-weight: 400;
	}
	padding: 1rem;
	>div{
		display: flex;
		// justify-content: center;
		overflow-x: auto;
		
	}

}

.card{
	width: 200px;
	margin: 0.3rem;
	cursor: pointer;
	transition: transform 0.5s;
	&:hover{
		transform:translateY(-10px) scale(1.05);
	}
}