@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;900&family=Varela+Round&display=swap');

*{
	margin: 0%;
	padding: 0%;
	font-family: "Roboto", sans-serif;
	
	&::-webkit-scrollbar{
		display: none;
	}
}

a,h2{
	color: white;
}
a{
	
	text-decoration:none;
}

$bg:rgb(16,16,16);
.header{
	height: 100px;
	background-color: $bg;
    display: flex;
	align-items: center;
    padding: 0.5rem;
    box-sizing: border-box;
	justify-content: space-between;
	>img{
		height: 95%;
		// filter: blur(px);
	}
	>div{
		// border: 1px solid red;
		width: 100%;
		margin-left: 4vmax;
		>a{
			margin: 3vmax;
			font-size: 1.2rem;
			font-weight: 100;
		}
	}
	
	>svg{
		color: white;
		font-size: 1.5rem;
		cursor: pointer;
		&:hover{
			color: rgb(216,216,216);
		}
	}
	
}
